<template>
  <div class="main">
    <!---->
    <header class="homeHerder">
      <top-module></top-module>
    </header>
    <!---->
    <nav class="home-nav">
      <navigation-bar></navigation-bar>
    </nav>
    <!---->
    <section class="collection-title"></section>
    <!---->
    <section class="collection-list">
      <span></span>
      <span></span>
    </section>
    <!---->
    <section class="collection-title"></section>
    <!---->
    <section class="collection-structure">
        <div class="collection-structurei"><img src="../../../assets/images/productCenter/pr_11.jpg" alt=""></div>
    </section>
    <!---->
    <section class="collection-scenarios">
        <img src="../../../assets/images/productCenter/pr_12.jpg" alt="">
    </section>
    <!---->

    <!-- <div style="height:200px;"></div> -->
    <!---->
    <footer class="home-footer">
      <footer-navigation></footer-navigation>
    </footer>
    <!---->
  </div>
</template>

<script>
import topModule from "../../../components/topModule.vue";
import navigationBar from "../../../components/navigationBar.vue";
import footerNavigation from "../../../components/footerNavigation.vue";
export default {
  data() {
    return {};
  },
  //
  components: {
    topModule,
    navigationBar,
    footerNavigation
  }
};
</script>


<style scoped>
.collection-scenarios{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 900px;
    margin-top: 100px;
}
.collection-scenarios img{
    width: 100%;
    height: 100%;
}
.main{
    background: #f7f7f7;
}
.collection-structurei{
    width: 1352px;
    height: 690px;
}
.collection-structurei img{
    width: 100%;
    height: 100%;
}
.collection-structure{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}
.collection-list span:nth-child(1) {
  line-height: 30px;
  text-indent: 2rem;
}
.collection-list span:nth-child(2) {
  line-height: 30px;
  text-indent: 2rem;
}
.collection-list {
  display: flex;
  flex-direction: column;
  padding: 0 200px;
  text-align: left;
}
.collection-title {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 100px;
  padding-bottom: 70px;
  font-weight: bold;
  font-size: 30px;
  color: #353232;
}
.homeHerder {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-nav {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-footer {
  display: flex;
  flex-direction: row;
  width: 100%;
}
</style>



