<template>
  <div class="main">
    <!---->
    <header class="homeHerder">
      <top-module></top-module>
    </header>
    <!---->
    <nav class="home-nav">
      <navigation-bar></navigation-bar>
    </nav>
    <!---->
    <section class="speech-lun">
      <img src="../../../assets/images/productCenter/pr_13.jpg" alt />
    </section>
    <!---->
    <section class="speech-title"></section>
    <!---->
    <section class="speech-voicelist">
      <span>ASRNLP110</span>
    </section>
    <!---->

    <section class="speech-application">
      <img src="../../../assets/images/productCenter/pr_14.jpg" alt />
      <span class="speech-title1"></span>
      <div class="speech-list1">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </section>
    <!---->
    <section class="speech-title"></section>
    <!---->
    <section class="speech-scene">
      <div class="speech-scenei">
        <img src="../../../assets/images/productCenter/pr_15.jpg" alt />
      </div>
    </section>

    <div style="height:200px;"></div>
    <!---->
    <footer class="home-footer">
      <footer-navigation></footer-navigation>
    </footer>
    <!---->
  </div>
</template>


<script>
import topModule from "../../../components/topModule.vue";
import navigationBar from "../../../components/navigationBar.vue";
import footerNavigation from "../../../components/footerNavigation.vue";
export default {
  data() {
    return {};
  },
  //
  components: {
    topModule,
    navigationBar,
    footerNavigation
  }
};
</script>

<style scoped>
.main{
    background: #f7f7f7;
}
.speech-scenei {
    padding: 0 100px;
  width: 100%;
  height: 860px;
}
.speech-scenei img {
  width: 100%;
  height: 100%;
}
.speech-scene {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.speech-list1 {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: absolute;
  color: #ffffff;
  bottom: 85px;
  font-size: 20px;
  font-weight: bold;
  padding: 0 4%;
}
.speech-list1 span {
  flex: 1;
}
.speech-title1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 70px;
  padding-bottom: 100px;
  font-weight: bold;
  font-size: 30px;
  color: #353232;
  position: absolute;
  color: #ffffff;
}
.speech-application {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 500px;
  margin-top: 100px;
  position: relative;
}
.speech-application img {
  width: 100%;
  height: 100%;
}
.speech-voicelist span:nth-child(1) {
  text-align: left;
  font-size: 16px;
  color: #1b1717;
  line-height: 27px;
  text-indent: 2rem;
}
.speech-voicelist {
  padding: 0 10%;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.speech-title {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 150px;
  padding-bottom: 100px;
  font-weight: bold;
  font-size: 30px;
  color: #353232;
}
.speech-lun {
  width: 100%;
  height: 500px;
}
.speech-lun img {
  width: 100%;
  height: 100%;
}
.homeHerder {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-nav {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-footer {
  display: flex;
  flex-direction: row;
  width: 100%;
}
</style>



