<script>
import topModule from "../../../components/topModule.vue";
import navigationBar from "../../../components/navigationBar.vue";
import footerNavigation from "../../../components/footerNavigation.vue";
export default {
  data() {
    return {};
  },
  //
  components: {
    topModule,
    navigationBar,
    footerNavigation
  }
};
</script>


<style scoped>
.micro-performancei{
    width: 100%;
    /* height: 1300px; */
    display: flex;
    flex-direction: row;
}
.micro-performancei img{
    width: 100%;
    height: 100%;
}
.micro-performance{
    display: flex;
    flex-direction: row;
    padding-top: 50px;
    background: #f7f7f7;
}
.micro-title1m span:nth-child(1){

    display: flex;
