    flex-direction: row;
    text-align: left;
    margin-left: 100px;
    color: #141010;
    line-height: 30px;
    text-indent: 2rem;
}
.micro-title1m{
    display: flex;
    flex-direction: row;
    background: #f7f7f7;
    padding: 0 200px;
}
.micro-title1{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 150px;
    padding-bottom: 100px;
    font-weight: bold;
    font-size: 30px;
    color: #353232;
    background: #f7f7f7;
}
.micro-lunimg{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 500px;
}
.micro-lunimg img{
    width: 100%;
    height: 100%;
}
.homeHerder {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-nav {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.home-footer {
  display: flex;
  flex-direction: row;
  width: 100%;
}
</style>


